AA,CACA,qBAAA,CACA,kBAAA,CACA,sBAAA,CAGJ,YACI,wDAAA,CACA,YAAA,CACA,WAAA,CACA,YAAA,CACA,gBAAA,CAGJ,aACI,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,aPvEe,COwEf,cAAA,CACA,eAAA,CAGJ,kBACI,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,aP9EkB,CO+ElB,cAAA,CACA,eAAA,CCtFJ,MACI,wBRDU,CQEV,WAAA,CACA,YAAA,CACA,aAAA,CAGJ,QACI,YAAA,CACA,6BAAA,CACA,kBAAA,CACA,gBAAA,CAGJ,aACI,cAAA,CACA,eAAA,CACA,aRXkB,CQYlB,cAAA,CAGJ,YACI,YAAA,CACA,sBAAA,CACA,kBAAA,CACA,QAAA,CAGJ,YACI,YAAA,CACA,MAAA,CAGJ,cACI,YAAA,CACA,UAAA,CACA,cAAA,CACA,cAAA,CACA,kBAAA,CACA,iBAAA,CAGJ,SACI,cAAA,CACA,aRjCS,CQkCT,gBAAA,CAGJ,cACI,YAAA,CACA,0BAAA,CACA,MAAA,CAGJ,aACI,SAAA,CACA,WAAA,CACA,gBAAA,CACA,iBAAA,CACA,eAAA,CACA,qBAAA,CAGJ,YACI,WAAA,CACA,wBR3DkB,CQ4DlB,WAAA,CACA,gBAAA,CACA,aR5DS,CQ6DT,WAAA,CACA,iBAAA,CACA,gBAAA,CACA,cAAA,CAGJ,kBACI,YAAA,CACA,gBAAA,CACA,cAAA,CAGJ,gBACI,gBAAA,CACA,cAAA,CACA,wBR5ES,CQ+Eb,iBACI,gBAAA,CACA,cAAA,CACA,wBRpFkB,CQqFlB,aRnFS,CQoFT,WAAA,CACA,iBAAA,CACA,UAAA,CAGJ,iBACI,aAAA,CACA,gBAAA,CACA,cAAA,CACA,wBR/FkB,CQgGlB,aR9FS,CQ+FT,WAAA,CACA,iBAAA,CACA,UAAA,CAKJ,qBACI,aAAA,CACA,iBAAA,CACA,OAAA,CACA,MAAA,CACA,QAAA,CACA,UAAA,CACA,wBR/GkB,CQgHlB,UAAA,CACA,6BAAA,CACA,cAAA,CAGJ,sDAEI,UAAA","sourcesContent":["*,\n*::before,\n*::after {\n    padding: 0;\n    margin: 0;\n    border: 0;\n    box-sizing: border-box;\n    font-size: 10px;\n}\n\nul,\nol,\nli {\n    list-style: none;\n}\n\na {\n    text-decoration: none;\n    color: $color-link;\n}\n","$color-light: #f5f5f5;\n$color-black: #000000;\n$color-primary: #ff5a00;\n\n$color-additional: #650e70;\n\n$color-primary-light: #ff8e08;\n\n$color-text: #f5f5f5;\n$color-dark: #303030;\n$color-border: #757575;\n$color-link: #422779;\n$color-hovered: #ff5a00;\n\n$color-body: #ff8e08;\n$color-error: #e9a4a2;\n$color-invalid-body: #ede5e5;\n$color-valid: #0ff12e45;\n$color-valid-body: #ffe9084f;\n\n$color-overlay: rgba(80, 80, 80, 0.4);\n","html {\n    font-size: 10px;\n    font-family: 'Quicksand', sans-serif;\n\n    @include media-tablet {\n        font-size: 6px;\n    }\n}\n\nbody {\n    background-color: $color-body;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n}\n\n.title {\n    @include heading-1($color: #218888, $style: italic);\n}\n\n.basic-text {\n    @include basic-text($color: #650e70, $style: normal);\n}\n\n.text-register {\n    font-family: 'Quicksand', sans-serif;\n    font-weight: 700;\n    font-size: 1.1rem;\n    margin-bottom: 4%;\n}\n\n.link_to_pages {\n    color: $color-primary-light;\n    font-weight: 700;\n    font-size: 1.1rem;\n    cursor: pointer;\n}\n","@mixin media-narrowing($media-narrowing) {\n    @media (max-width: $media-narrowing) {\n        @content;\n    }\n}\n\n@mixin desktop-small-width {\n    @media (max-width: $desktop-small-width) {\n        @content;\n    }\n}\n\n@mixin media-tablet {\n    @media (max-width: $tablet-width) {\n        @content;\n    }\n}\n\n@mixin mobile-big-width {\n    @media (max-width: $mobile-big-width) {\n        @content;\n    }\n}\n@mixin mobile-small-width {\n    @media (max-width: $mobile-small-width) {\n        @content;\n    }\n}\n","@mixin heading-1($color: $color-text, $style: normal) {\n    font: {\n        family: 'Open Sans', sans-serif;\n        size: 6.4rem;\n        style: $style;\n        weight: 800;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin basic-text($color: $color-text, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 6.4rem;\n        style: $style;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input-placeholder($color: $color-border, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.2rem;\n        style: $style;\n        weight: 400;\n    }\n\n    line-height: 105%;\n    color: $color;\n    padding: 0.2rem;\n}\n\n@mixin input-text($color: $color-link, $size: 2rem) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: $size;\n        style: normal;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin button-text($color: $color-primary, $style: normal) {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 2.4rem;\n        style: $style;\n        weight: 800;\n    }\n\n    line-height: 105%;\n    color: $color;\n}\n\n@mixin input() {\n    width: 21.4rem;\n    height: 3.3rem;\n    border: $color-border 1px solid;\n    box-shadow: 3px 3px 5px -2px rgba(0, 0, 0, 0.75);\n    padding-left: 0.5rem;\n    padding-bottom: 0.5rem;\n    cursor: pointer;\n    @include input-text;\n    &::placeholder {\n        @include input-placeholder($style: italic);\n    }\n}\n\n@mixin button-basic($color-bg: $color-light) {\n    display: block;\n    width: 15rem;\n    height: 8rem;\n    background-color: $color-bg;\n    border-radius: 1rem;\n    border: solid 2px $color-border;\n    text-align: center;\n    @include button-text;\n    cursor: pointer;\n\n    &:hover {\n        background-color: $color-additional;\n        color: $color-light;\n    }\n\n    &.active {\n        background-color: $color-primary;\n        color: $color-additional;\n    }\n}\n",".button-basic {\n    @include button-basic;\n}\n",".registration_container {\n    background-color: $color-light;\n    flex-basis: 50%;\n    margin: 15rem;\n    border-radius: 3rem;\n    box-shadow: 10px 10px 25px -4px rgba(0, 0, 0, 0.58);\n}\n\n.registration {\n    display: flex;\n    flex-direction: column;\n    padding: 4rem 10rem;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n}\n\n.logo_registration {\n    background-image: url('img/apoint.png');\n    display: block;\n    width: 11.4rem;\n    height: 3.4rem;\n    background-repeat: no-repeat;\n    background-size: contain;\n}\n\n.registration__error {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1.2rem;\n        style: normal;\n        weight: 400;\n    }\n\n    line-height: 105%;\n    color: $color-error;\n}\n\n.label_basic {\n    display: flex;\n    gap: 1rem;\n    justify-content: center;\n    align-items: center;\n}\n\n.input_basic {\n    @include input;\n\n    &:focus {\n        background-color: #ff900893;\n        color: $color-primary;\n        border-color: $color-primary;\n        outline: 0;\n    }\n\n    &:invalid {\n        border: 2px solid $color-error;\n        color: $color-dark;\n        background-color: $color-invalid-body;\n    }\n\n    &:valid {\n        border: 2px solid $color-valid;\n        color: $color-primary;\n        background-color: $color-valid-body;\n    }\n}\n\n.svg_user {\n    background-image: url('img/icons/user.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_users {\n    background-image: url('img/icons/users.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_email {\n    background-image: url('img/icons/mail.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_password {\n    background-image: url('img/icons/lock.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_calendar {\n    background-image: url('img/icons/calendar.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.svg_map_pin {\n    background-image: url('img/icons/map-pin.svg');\n    display: block;\n    width: 2rem;\n    height: 2rem;\n}\n\n.address_block {\n    border: $color-border 2px solid;\n    padding: 2rem;\n    display: flex;\n    flex-direction: column;\n    align-items: flex-end;\n    justify-content: space-between;\n    gap: 0.5rem;\n}\n\n.address_span {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 1rem;\n        style: normal;\n        weight: 600;\n    }\n\n    line-height: 105%;\n    color: $color-primary;\n}\n\n.checkbox_span {\n    @include input-text($size: 1.5rem);\n}\n\n.select_block {\n    display: flex;\n    gap: 1rem;\n    align-items: center;\n    justify-content: center;\n}\n\n.select_basic {\n    width: 10.5rem;\n    height: 3rem;\n    @include input-text($size: 1.5rem);\n    border: $color-border 2px solid;\n    border-radius: 0.6rem;\n    cursor: pointer;\n    &:focus {\n        border-color: $color-primary;\n        outline: 0;\n    }\n\n    option {\n        @include input-text($size: 1.5rem);\n        background-color: hsla(33, 100%, 52%, 0.576);\n        color: $color-primary;\n    }\n}\n\n.address_title {\n    font: {\n        family: 'Quicksand', sans-serif;\n        size: 2rem;\n        style: normal;\n        weight: 500;\n    }\n\n    line-height: 105%;\n    color: $color-primary;\n    text-align: center;\n    border-bottom: 1px solid $color-dark;\n    width: 20rem;\n    display: none;\n}\n\n.button_registration {\n    font-family: 'Open Sans', sans-serif;\n    width: 26rem;\n    font-weight: 500;\n    display: block;\n    height: 5.7rem;\n    background-color: $color-primary;\n    color: $color-link;\n    font-size: 1.9rem;\n    border-radius: 1rem;\n    border: solid 2px $color-link;\n    text-align: center;\n    cursor: pointer;\n    margin-top: 2rem;\n\n    &:hover {\n        background-color: $color-additional;\n        color: $color-light;\n    }\n\n    &:disabled {\n        background-color: darkgrey;\n        cursor: not-allowed;\n    }\n}\n\n.shipping_address {\n    display: none;\n    flex-direction: column;\n    align-items: flex-end;\n    justify-content: space-between;\n    gap: 0.5rem;\n}\n\n.billing_address {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-end;\n    justify-content: space-between;\n    gap: 0.5rem;\n}\n\n.link_to_loginPages {\n    font-family: 'Open Sans', sans-serif;\n    font-weight: 500;\n    color: $color-link;\n    font-size: 1.9rem;\n    text-align: center;\n    cursor: pointer;\n    margin-top: 2rem;\n\n    &:hover {\n        background-color: $color-additional;\n        color: $color-light;\n    }\n}\n",".login-page {\n    display: flex;\n    background-color: $color-text;\n    width: 300px;\n    align-items